import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

export default function Images(props) {

    const [config, setConfig] = useState({ index: 0, isOpen: false });
    const [images, setImages] = useState([]);
    const [imageUrls, setImageUrl] = useState([]);


    useEffect(() => {
        setImages(props.images);
        if (props.images && props.images.legth > 0) {
            let urls = [];

            props.images.map((row) => {
                urls.push(row.url);
            })
            setImageUrl(urls);
        }
    }, [props.images])


    return <div>
        {images && images.map((img, ind) => {
            return <span
                key={ind}
                className="img-gallery-fluid"
                onClick={() => { setConfig({ index: ind, isOpen: true }); }}
            >
                <img
                    src={img.url}
                    className="img-fluid rounded"
                />
            </span>;
        })}

        {config.isOpen && (
            <Lightbox
                mainSrc={imageUrls[config.index]}
                nextSrc={imageUrls[(config.index + 1) % imageUrls.length]}
                prevSrc={imageUrls[(config.index + imageUrls.length - 1) % imageUrls.length]}
                onCloseRequest={() => setConfig({ ...config, isOpen: false })}
                onMovePrevRequest={() =>
                    setConfig({
                        ...config,
                        index: (config.index + imageUrls.length - 1) % imageUrls.length,
                    })
                }
                onMoveNextRequest={() =>
                    setConfig({
                        ...config,
                        index: (config.index + 1) % imageUrls.length,
                    })
                }
            />
        )}
    </div>
}