export function indexOfObject(arr, key, val) {
    var elm = arr.find(function (row) {
        if (row[key] === val) {
            return row;
        }
    }, key, val);
    return arr.indexOf(elm);
}

export function dateFormat(obj) {
    if (obj) {
        let date = new Date(obj);
        return date.getDate() + '-' + date.getMonth() + '-' + date.getFullYear();
    } else {
        return 'N/A';
    }
}

export function addDateTimePicker(selecter, onChnageCallback, format = 'YYYY-MM-DD', etc = {}) {
    window.jQuery(selecter).datetimepicker({
        format: format,
    }).on('dp.change', function (e) {
        //Reference link https://stackoverflow.com/questions/27398028/bootstrap-datetimepicker-trigger-dp-change-on-class
        if (e.target.tagName === 'DIV') {
            e.target = e.target.querySelector('input');
        }
        onChnageCallback(e);
    });
}

export function jsonToFormData(item) {
    let form_data = new FormData();
    for (var key in item) {
        form_data.append(key, item[key]);
    }
    return form_data;
}