import React, { useState, useEffect } from "react";
import Modal from 'react-bootstrap/Modal';

export default function FullNameModal(props) {

    const [form, setForm] = useState({...props.name});

    const handleChange = (e) => {
		setForm({
			...form,
			[e.target.name]: e.target.value
		});
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        props.setName(form);
    }

    useEffect(()=>{
        setForm({...props.name});
    }, [props.name]);



    return (<>
        <Modal
            show={props.show}
            onHide={props.onHide}
            backdrop="static"
            keyboard={false}
            className="animate__animated animate__fadeInUp custom-model"
        >
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-body">
                        <nav>
                            <div className="float-left">
                                <span onClick={props.onHide} aria-label="Close" className="back-btn"><img src="/assets/img/down-icon.svg" /></span>
                            </div>
                            <div className="agnacare_logo heading height48 zIndexless"><span className="text-center font18 margin-none">Full Name</span></div>
                            <div className="clearfix" />
                        </nav>
                        <form onSubmit={handleSubmit}>
                            <label className="form-label color-doctor weight500 pd-top-30">First Name</label>
                            <input type="text" required={true} className="form-control input-style" name="fname" onChange={handleChange} value={form.fname} placeholder="First Name" />
                            <label className="form-label color-doctor weight500 pd-top-30">Middle Name</label>
                            <input type="text" className="form-control input-style" name="mname" onChange={handleChange} value={form.mname} placeholder="Middle Name" />
                            <label className="form-label color-doctor weight500 pd-top-30">Last Name</label>
                            <input type="text" required={true} className="form-control input-style" name="lname" onChange={handleChange} value={form.lname} placeholder="Last Name" />
                            <button type="submit" className="btn btn-doctor-color ripple btn-block mg-top-20">Save</button>
                        </form>
                    </div>
                </div>
            </div>
        </Modal>
    </>);
}