import React, { useEffect } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import { useDispatch } from 'react-redux';
import { flashClear } from "../../__store/actions";


export function Toast(props) {

    const dispatch = useDispatch();
    useEffect(() => {
        if (props.msg && props.type == 'E') {
            errAlert();
        }else if(props.msg && props.type == 'S'){
            successAlert();
        }

        setTimeout(()=>{
            dispatch(flashClear());
        }, 100)
    }, [props.msg]);

    function successAlert() {
        toast.success(props.msg, {
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: false,
            progress: undefined,
        });
    }

    function errAlert() {
        toast.error(props.msg, {
            position: "top-center",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: false,
            progress: undefined,
        });
    }

    return (
        <div>
            <ToastContainer
                position="top-center"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable={false}
                pauseOnHover
            />
        </div>
    );
}