import store from "../__store";
import { login, logout as logoutRedux, authLoaded } from "../__store/actions";

export const initAuth = () => {
  let user = localStorage.getItem("user") || null;
  if (user) {
      user = JSON.parse(user);
      store.dispatch(login(user));
  }else{
    store.dispatch(authLoaded());
  }
};

export const loginValidate = props => {
  if (props.auth.is_login === false) {
    props.history.push("/login");
  }
};


export const logout = () => {
  localStorage.removeItem("authToken");
  localStorage.removeItem("user");
  store.dispatch(logoutRedux());
};