import React, { useEffect } from "react";
import { shallowEqual, useSelector } from 'react-redux';
import { Toast } from "../utils";
import BottomList from "./bottom-list/bottom-list";
import { Loader } from "../utils";


function BlankLayout(props) {

    const { flash, loader } = useSelector(state => ({
        flash: { ...state.flash },
        loader: { ...state.loader }
    }), shallowEqual);

    return (
        <div>
            <Toast {...flash} />
            <Loader isLoading={loader.isLoading} />
            {props.children}
            <BottomList />
        </div>
    );
}

export default BlankLayout;
