import * as actionTypes from "../../__constant/reducer";

const initialState = {
  type: '',
  msg: null
};

const flash = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.FLASH_ERR:
      return {
        ...state,
        type: 'E',
        msg: action.payload
      };
    case actionTypes.FLASH_SUCCESS:
      return {
        ...state,
        type: 'S',
        msg: action.payload
      };
    case actionTypes.FLASH_REMOVE:
      return {
        ...state,
        type: '',
        msg: null
      };
    default:
      return state;
  }
};

export default flash;
