import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { propTypes } from "react-bootstrap/esm/Image";

export default function DoctorList(props) {

    return (
        <div className="container px-0">
            {props.doctors.map((row, ind) => {
                return <div key={ind} className="search-list mb-4">
                    <Link to={`/doctor/profile?id=${row.doctor_id}`}>
                        <div className="media doctor-profile">
                            <div className="align-self-start mr-3 doctor-img-layout">
                                <img src={row.profile_pic} className="doctor-img border-doctor-profile-search" />
                                <div className="layout-bottom text-center">
                                    <img src="/assets/img/track_icon.svg" />
                                    <span className="text-white">{row.distance || 0}km</span>
                                </div>
                            </div>
                            <div className="media-body mg-top-10">
                                <div className="specialization-mark mgbottom4">
                                    <img src={row.dept_icon} />
                                    <span className="font14 weight500 color-doctor">{row.profile_title}</span>
                                </div>
                                <h6 className="color-dark mt-0 mgbottom4 weight600">
                                    {row.name}
                                </h6>
                                <div className="mgbottom4" style={{ lineHeight: '18px' }}>
                                    <span className="color-grey font14">{row.speciality}</span>
                                </div>
                                <div className={`rating-star mg-top-15 ${(row.stars > 3 ? '' : 'norating')}`}>
                                    <ul className="float-left">
                                        {[1, 2, 3, 4, 5].map((val, ind) => {
                                            let img_src;
                                            if ((val - row.stars) == .5) {
                                                img_src = '/assets/img/half_star_icon.svg'
                                            } else if (val > row.stars) {
                                                img_src = '/assets/img/blank_icon.svg'
                                            } else {
                                                img_src = '/assets/img/star_icon.svg';
                                            }

                                            return <li key={ind} className='star'><img src={img_src} /></li>;
                                        })}
                                    </ul>
                                    <span className={`font14 pd-left-5 ${row.stars > 3 ? 'color-orange' : 'color-grey'}`}>{row.no_of_users_rated || 0}</span>
                                    <div className="clearfix" />
                                </div>
                            </div>
                        </div>
                    </Link>
                    <div className="search-list-bottom">
                        <Link to="doctor-profile.html">
                            <div className="float-left">
                                <img className="cur-icon" src={row.currency_icon} />
                                <span className="font12 color-dark">{row.min_charges} - {row.max_charges} {row.currency_abbr}</span>
                            </div>
                        </Link>
                        <div className="float-right">
                            <a href="doctor-profile.html"></a>
                            <a
                                data-toggle="modal"
                                data-target="#booking"
                                className="btn btn-doctor-color btn-block btn-sm"
                                role="button"
                                aria-pressed="true"
                            >
                                Book Now
                            </a>
                        </div>
                        <div className="clearfix" />
                    </div>
                </div>
            })}
        </div>
    );
}
