import axios from "axios";
import store from "./../__store";
import { logout } from "../__store/actions/";
import { loader, alert as alertMsg } from "../__helpers";
let $=window.$;

const getTimezoneOffset=()=>{
	var d=new Date();
	return d.getTimezoneOffset();
}
const paramSerializeData=(data, method)=>{
	let tzKey='timezone_offset';
	let tzVal=getTimezoneOffset();

	let timezone_offset=getTimezoneOffset();
	if(typeof data === "object"){
		if(typeof data.append !== "function"){
			data[tzKey]=tzVal;
			if(method==='post'){
				data=$.param(data);
			}
		}else{
			data.append(tzKey, tzVal);
		}
	}else{
		data=data+'&'+tzKey+'='+encodeURIComponent(tzVal);
	}
	return data;
}

const postRequestWithoutToken = (reqUrl, params, showLoader=false) => {
	if(showLoader){
		loader(true);
	}

	return axios
		.post(reqUrl, params)
		.then(response => {
			if(showLoader){
				loader(false);
			}
			if (response.data.code !== 200) {
				throw (response);
			}
			return response.data;
		})
		.catch(error => {
			if(showLoader){
				loader(false);
			}
			
			if(error.data){
				alertMsg(error.data.msg, 'E');
				throw (error.data);
			}

			alertMsg(error, 'E');
			error.result = {};
			throw (error)
		});
};

const post = (reqUrl, params, showLoader=false) => {
	if(showLoader){
		loader(true);
	}
	var config = {
		headers: {
			'AuthToken': (localStorage.getItem("authToken") || ''),
		}
	};

	params=paramSerializeData(params, 'post');

	return axios
		.post(reqUrl, params, config)
		.then(response => {
			if(showLoader){
				loader(false);
			}
			if (response.data.code !== 200) {
				throw (response);
			}
			return response.data;
		})
		.catch(error => {
			if(showLoader){
				loader(false);
			}
			
			let data = {};
			if ((data = error.response) || (data = error.data)) {
				
				if(error.response){
					data = data.data;
				}

				alertMsg(data.message, 'E');
				
				if (data.code === 401) {
					store.dispatch(logout());
				} else {
					throw (data);
				}
			} else {
				
				alertMsg(error.message || error, 'E');
				throw (error.message)
			}
		});
};

const deleteWithToken = (reqUrl) => {
	var config = {
		headers: {
			'Authorization': "Bearer " + localStorage.getItem("authToken"),
			'Content-Type': 'application/json',
			'Accept': 'application/json'
		}
	};

	return axios
		.delete(reqUrl, config)
		.then(response => {
			return response.data;
		})
		.catch(error => {
			if (error.response) {
				alertMsg(error.response.data.message, 'E');
				if (error.response.data.code === 401 || error.response.data.status === 401) {
					store.dispatch(logout());
				}
				return error.response.data;
			} else {
				alertMsg("Internal Server Error", 'E');
				return { code: 500 }
			}
		});
};

const getWithoutToken = (reqUrl, params, showLoader) => {
	if(showLoader){
		loader(false);
	}

	return axios
		.get(reqUrl, params)
		.then(response => {
			if(showLoader){
				loader(false);
			}
			if (response.data.code !== 200) {
				throw (response);
			}
			return response.data;
		})
		.catch(error => {
			if(showLoader){
				loader(false);
			}
			
			if(error.data){
				alertMsg(error.data.message, 'E');
				throw (error.data);
			}

			alertMsg(error.message, 'E');
			error.result = {};
			throw (error)
		});
};

const getRequestWithToken = (reqUrl, params = {}) => {
	var config = {
		headers: {
			'Authorization': "Bearer " + localStorage.getItem("authToken"),
			'Content-Type': 'application/json',
			'Accept': 'application/json'
		},
		params: params
	};

	return axios
		.get(reqUrl, config)
		.then(response => {
			return response.data;
		})
		.catch(error => {
			if (error.response) {
				alertMsg(error.response.data.message, 'E');
				if (error.response.data.code === 401 || error.response.data.status === 401) {
					store.dispatch(logout());
				}
				return error.response.data;
			} else {
				alertMsg("Internal Server Error", 'E');
				return { code: 500 }
			}
		});
};

export default { postRequestWithoutToken: postRequestWithoutToken, getWithoutToken: getWithoutToken, post: post, deleteWithToken: deleteWithToken, getRequestWithToken: getRequestWithToken };
