import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { shallowEqual, useSelector, useDispatch } from 'react-redux';

import { alertMsg, jsonToFormData } from "../../__helpers";
import { sentOtp, login as loginAction } from "../../__store/actions";
import service from "../../services/service";


export default function Otp(props) {

	const [form, setForm] = useState({ otp: '' });

	const { auth } = useSelector(state => ({
		auth: { ...state.auth }
	}), shallowEqual);

	const dispatch = useDispatch();

	function resendOtp() {

		service.postRequestWithoutToken("get-otp", jsonToFormData({ email: auth.user.email }), true).then(resp => {
			alertMsg(resp.msg, 'S');
			dispatch(sentOtp({
				email: auth.user.email,
				otp_key: resp.otp_key
			}));

		}).catch(e => {});
	}

	function emailShortCode() {
		if (auth.user.email) {
			let atIndex = auth.user.email.indexOf("@");
			let firstChar = auth.user.email.substring(0, 1);
			let lastWord = auth.user.email.substring(atIndex - 1);
			return `${firstChar}*****${lastWord}`;
		}
		return '';
	}

	const handleChange = (e) => {
		setForm({
			...form,
			[e.target.name]: e.target.value
		});
	}

	function login(e) {

		if(e){
			e.preventDefault();
		}

		let form_data = {
			...form,
			email: auth.user.email,
			otp_key: auth.user.otp_key
		}

		service.postRequestWithoutToken("login", jsonToFormData(form_data), true).then(resp => {

			dispatch(loginAction({
				...resp.user_dtl
			}));

			localStorage.setItem('authToken', resp.authtoken);
			localStorage.setItem('user', JSON.stringify(resp.user_dtl));

			props.history.push("/profile");
		}).catch(e => {});
	}

	useEffect(() => {
		if (auth.currentState != 'OTP_SENT') {
			alertMsg("First Enter Your Email", "E");
			props.history.push("/login");
		}
	}, []);

	return (
		<div className="mobile-responsive">
			<div className="container mg-top-20">
				<Link to="/login" className="back-btn"><img src="/assets/img/back-icon.svg" /></Link>
				<h3 className="mg-top-40 weight700">Confirm Your Email</h3>
				<p className="mg-top-20">Enter the 6-digit code Agnacare just sent to {emailShortCode()}</p>

				<form onSubmit={login}>
					<input type="text" name="otp" value={form.otp} onChange={handleChange} className="form-control input-style" placeholder="OTP" required={true} />

					<div className="mg-top-10">
						<p className="float-left color-grey">Didnt receive the code?</p>
						<p className="float-right"><span onClick={resendOtp} className="link-color-doctor">Resend Code</span></p>
					</div>
					<div className="clearfix"></div>
					<button type="submit" className="btn btn-doctor-color ripple btn-block mg-top-20">Submit</button>
				</form>
			</div>
		</div>
	);
}
