import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch } from 'react-redux';
import { jsonToFormData } from "../../__helpers";
import { sentOtp, flashSuccess, flashErr, login as loginAction } from "../../__store/actions";
import service from "../../services/service";


export default function Login(props) {

	const [form, setForm] = useState({ email: '' });

	const dispatch = useDispatch();

	const handleChange = (e) => {
		setForm({
			...form,
			[e.target.name]: e.target.value
		});
	}

	function sendOtp(e) {
		e.preventDefault();
		service.postRequestWithoutToken("get-otp", jsonToFormData({ email: form.email }), true).then(resp => {
			
			dispatch(flashSuccess(resp.msg));

			dispatch(sentOtp({
				email: form.email,
				otp_key: resp.otp_key
			}));

			props.history.push("/otp");

		}).catch(e => {
			dispatch(flashErr(e.msg));
		});
	}

	return (
		<div>
			<div className="mobile-responsive">
				<div className="container pd-top-40">
					<img className="login-logo" src="assets/img/fav-logo.svg" />
					<h3 className="pd-top-20 weight700">Log In to Agnacare</h3>

					<form onSubmit={sendOtp}>
						<label className="form-label color-doctor weight500 pd-top-30">Email</label>
						<input type="text" name="email" value={form.email} onChange={handleChange} className="form-control input-style" placeholder="Email" />
						<span className="color-grey mg-top-10 weight300">Well send you an email that will instantly log in</span>

						<button className="btn btn-doctor-color ripple btn-block mg-top-20" role="button" aria-pressed="true">Log In</button>
					</form>

					<div className="mg-top-20 text-center divider">
						<div className="divider-text"><span>OR</span></div>
						<div className="border-bottom"></div>
					</div>

					<div className="mg-top-30">
						<button type="button" className="btn btn-border btn-facebook ripple btn-block">Continue with Facebook</button>
						<button type="button" className="btn btn-border btn-google ripple btn-block mg-top-20">Continue with Google</button>
					</div>

					<div className="mg-top-30">
						<span className="color-grey">Dont have an account?</span>
						<Link to="/register" className="link-dark">Sign Up</Link>
					</div>
				</div>
			</div>
		</div>
	);
}
