import React, { useEffect } from 'react';

import { shallowEqual, useSelector } from 'react-redux';
import Routes from "../src/__routes/routes";
import { initAuth } from "./__helpers";

function App(props) {

  const { auth } = useSelector(state => ({
		auth: { ...state.auth }
	}), shallowEqual);


  useEffect(()=>{
    initAuth();
  },[]);

  return (
    <div className="App">
      {auth.currentState != false && <Routes />}
    </div>
  );
}

export default App;
