import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import service from "../../services/service";
import { alertMsg, jsonToFormData } from "../../__helpers";

export default function Medical(props) {

    const [form, setForm] = useState({});

    /* Page Data */
    const handleChange = (e) => {
        setForm({
            ...form,
            [e.target.name]: e.target.value
        });
    }

    /* Apis Call */

    const saveMedicalInfo = (e) => {
        e.preventDefault();
        service.post("update-medical-info", jsonToFormData(form), true).then(resp => {
            alertMsg(resp.msg, "S");
            props.history.push("/profile");
        }).catch(e => { alertMsg(e.msg, "E"); })
    }

    useEffect(() => {
        service.post("user-detail", {}, true).then(resp => {
            setForm(resp.result.medical_info);
        }).catch(e => { });
    }, [])

    return (
        <div>
            <div>
                <div className="mobile-responsive">
                    <div className="container">
                        <form onSubmit={saveMedicalInfo}>
                            <nav>
                                <div className="float-left">
                                    <Link to="/profile" className="back-btn">
                                        <img src="/assets/img/back-icon.svg" />
                                    </Link>
                                </div>
                                <div className="agnacare_logo heading height48 zIndexless">
                                    <span className="text-center font18 margin-none">Medical</span>
                                </div>
                                <div className="float-right">
                                    <button type="submit" className="btn link-color-doctor text-decoration">
                                        Save
                                    </button>
                                </div>
                                <div className="clearfix" />
                            </nav>

                            <div>
                                <label className="form-label color-doctor weight500 pd-top-20">
                                    Allergies
                                </label>
                                <input type="text" name="allergies" value={form.allergies || ''} onChange={handleChange} className="form-control input-style" placeholder="Enter your allergies"/>
                            </div>

                            <div>
                                <label className="form-label color-doctor weight500 pd-top-20">
                                    current medication
                                </label>
                                <input type="text" name="current_med" value={form.current_med || ''} onChange={handleChange} className="form-control input-style" placeholder="Enter your current medication" />
                            </div>

                            <div>
                                <label className="form-label color-doctor weight500 pd-top-20">
                                    Past Medication
                                </label>
                                <input type="text" name="past_med" value={form.past_med || ''} onChange={handleChange} className="form-control input-style" placeholder="Enter your past medication" />
                            </div>
                            
                            <div>
                                <label className="form-label color-doctor weight500 pd-top-20">
                                    Disease
                                </label>
                                <input type="text" name="disease" value={form.disease || ''} onChange={handleChange} className="form-control input-style" placeholder="Enter your disease" />
                            </div>
                            
                            <div>
                                <label className="form-label color-doctor weight500 pd-top-20">
                                    Injuries
                                </label>
                                <input type="text" name="injuries" value={form.injuries || ''} onChange={handleChange} className="form-control input-style" placeholder="Enter your injuries" />
                            </div>

                            <div>
                                <label className="form-label color-doctor weight500 pd-top-20">
                                    Family History
                                </label>
                                <input type="text" name="family_history" value={form.family_history || ''} onChange={handleChange} className="form-control input-style" placeholder="Enter your family history" />
                            </div>                          
                        </form>
                    </div>
                </div>
            </div>

        </div>
    );
}
