import * as actionTypes from "../../__constant/reducer";

export const login = userData => {
  return {
    type: actionTypes.LOG_IN,
    payload: userData
  };
};

export const authLoaded = () => {
  return {
    type: actionTypes.AUTH_LOADED,
    payload: {}
  };
};

export const sentOtp = data => {
  return {
    type: actionTypes.OTP_SENT,
    payload: data
  };
};

export const logout = () => {
  return {
    type: actionTypes.LOG_OUT
  };
};
