import * as actionTypes from "../../__constant/reducer";

export const flashSuccess = msg => {
  return {
    type: actionTypes.FLASH_SUCCESS,
    payload: msg
  };
};

export const flashErr = msg => {
  return {
    type: actionTypes.FLASH_ERR,
    payload: msg
  };
};

export const flashClear = () => {
  return {
    type: actionTypes.FLASH_REMOVE
  };
};
