import React from "react";
import { Route, Redirect } from "react-router-dom";
import store from "../__store";

export const AuthRoute = ({ component: Component, ...rest }) => <Route {...rest} render={props => (store.getState().auth.currentState == 'LOGGED_IN' ? <Component {...props} /> : <Redirect to="/login" />)} />;

export const AccessRoute = ({ component: Component, ...rest }) => <Route {...rest} render={props => ((rest.allowedRole.indexOf(store.getState().auth.user.role_id) > -1 && (store.getState().auth.loggingIn)) ? <Component {...props} /> : <Redirect to="/dashboard" />)} />;