import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import service from "../../services/service";
import { alertMsg, jsonToFormData } from "../../__helpers";

export default function Lifestyle(props) {

    const [form, setForm] = useState({});

    /* Page Data */
    const handleChange = (e) => {
        setForm({
            ...form,
            [e.target.name]: e.target.value
        });
    }

    /* Apis Call */

    const saveLifeStyle = (e) => {
        e.preventDefault();
        service.post("update-lifestyle", jsonToFormData(form), true).then(resp => {
            alertMsg(resp.msg, "S");
            props.history.push("/profile");
        }).catch(e => { alertMsg(e.msg, "E"); })
    }

    useEffect(() => {
        service.post("user-detail", {}, true).then(resp => {
            setForm(resp.result.lifestyle);
        }).catch(e => { });
    }, [])

    return (
        <div className="mobile-responsive">
            <div className="container">
                <form onSubmit={saveLifeStyle}>

                    <nav>
                        <div className="float-left">
                            <Link to="/profile" className="back-btn">
                                <img src="/assets/img/back-icon.svg" />
                            </Link>
                        </div>
                        <div className="agnacare_logo heading height48 zIndexless">
                            <span className="text-center font18 margin-none">Lifestyle</span>
                        </div>
                        <div className="float-right">
                            <button type="submit" className="btn link-color-doctor text-decoration">
                                Save
                        </button>
                        </div>
                        <div className="clearfix" />
                    </nav>

                    <div>
                        <label className="form-label color-doctor weight500 pd-top-20">
                            How often do you consume alcohol?
                        </label>
                        <input type="text" name="alcohol" value={form.alcohol} onChange={handleChange} className="form-control input-style" placeholder="Enter your answer" />
                    </div>

                    <div>
                        <label className="form-label color-doctor weight500 pd-top-20">
                            Do you use or do you have history of using tobacco?{" "}
                        </label>
                        <input type="text" name="tobbaco" value={form.tobbaco} onChange={handleChange} className="form-control input-style" placeholder="Enter your answer" />
                    </div>

                    <div>
                        <label className="form-label color-doctor weight500 pd-top-20">
                            How would you describe your current physical activity ?{" "}
                        </label>
                        <input type="text" name="physical_activity" value={form.physical_activity} onChange={handleChange} className="form-control input-style" placeholder="Enter your answer" />
                    </div>

                    <div>
                        <label className="form-label color-doctor weight500 pd-top-20">
                            What is your food preference?{" "}
                        </label>
                        <input type="text" name="food_preference" value={form.food_preference} onChange={handleChange} className="form-control input-style" placeholder="Enter your answer" />
                    </div>
                </form>
            </div>
        </div>
    );
}
