import React, { useState, useEffect } from "react";

import { jsonToFormData } from "../../__helpers";
import service from "../../services/service";
import { Link } from "react-router-dom";

import FilterModal from "./filter-modal";
import DoctorList from "./doctor-list";

export default function Search(props) {

  const [form, setForm] = useState({ keyword: '', lat: '', lng: '', city_id: '', location_id: '', department_id: '' });
  const [results, setResult] = useState([]);

  const [filterModal, setFilterModal] = useState(false);

  const search = (e) => {
    if (e) { e.preventDefault(); }

    service.postRequestWithoutToken("doctors", jsonToFormData(form), true).then(resp => {
      setResult(resp.result);
    }).catch(e => { })
  }

  const handleSearch = (e) => {
    setForm({ [e.target.name]: e.target.value });
  }

  const saveLocation = (location) => {
    setForm({ ...form, lat: location.coords.latitude, lng: location.coords.longitude });
  }

  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(saveLocation);
    }
  }, []);

  useEffect(() => {
    const searchParams = new URLSearchParams(props.location.search);

    if (searchParams.get("department_id")) {
      service.postRequestWithoutToken("doctors", jsonToFormData({ department_id: searchParams.get("department_id") }), true).then(resp => {
        setResult(resp.result);
      }).catch(e => { })
    }

  }, [props.location.search]);

  return (
    <div className="mobile-responsive">
      <div className="container">
        <nav>
          <div className="float-left"><Link to="/" className="back-btn">
            <img src="assets/img/back-icon.svg" /></Link>
          </div>
          <div className="agnacare_logo heading height48 zIndexless">
            <span className="text-center font18 margin-none">Search</span>
          </div>
          <div className="clearfix" />
        </nav>
        <form className="mg-top-20" onSubmit={search}>
          <div className="search-box input-group">
            <input type="search" name="keyword" value={form.keyword} onChange={handleSearch} className="form-control search" placeholder="Search for Specialities" />
            <div className="input-group-append">
              <button type="submit" className="input-group-text">
                <i className="fas fa-search"></i>
              </button>
            </div>
          </div>
        </form>

        {/* Filter  */}
        <div>
          <div className="mg-top-15">
            <div className="media">
              <span onClick={() => { setFilterModal(true) }} className="weight600 mr-3 btn btn-sm text-uppercase btn-outline-grey" >
                <img src="/assets/img/filter_icon.svg" /> Filter
              </span>
              <div className="media-body" />
            </div>
          </div>
          <div className="mg-top-15">
            <div className="media">
              <div className="media-body filter-overflow">
                <a data-toggle="modal" data-target="#filter" className="weight600 mr-2 btn btn-sm text-uppercase btn-outline-grey filter-active" >
                  <span>4</span>
                  Filter
                </a>
                <a href="" className="weight600 mr-2 btn btn-sm text-uppercase btn-outline-grey" >
                  Dentist <i className="fas fa-times-circle" />
                </a>
                <a href="" className="weight600 mr-2 btn btn-sm text-uppercase btn-outline-grey" >
                  Male Doctor <i className="fas fa-times-circle" />
                </a>
                <a href="" className="weight600 mr-2 btn btn-sm text-uppercase btn-outline-grey" >
                  Price <i className="fas fa-times-circle" />
                </a>
                <a href="" className="weight600 mr-2 btn btn-sm text-uppercase btn-outline-grey" >
                  Rating <i className="fas fa-times-circle" />
                </a>
              </div>
            </div>
          </div>
        </div>


        {results.length > 0 && <div className="shown-result">
          <span className="font13 color-grey weight600">Search Results {results.length}</span>
        </div>
        }

        <DoctorList doctors={results} />

      </div>

      <FilterModal show={filterModal} onHide={() => setFilterModal(false)} />
    </div>
  );
}
