import * as actionTypes from "../../__constant/reducer";

const initialState = {
  currentState: false,
  user: {}
};

const auth = (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.LOG_IN:
      return {
        ...state,
        currentState: actionTypes.LOG_IN,
        user: action.payload
      };
    case actionTypes.AUTH_LOADED:
      return {
        ...state,
        currentState: actionTypes.AUTH_LOADED,
        user: action.payload
      };
    case actionTypes.OTP_SENT:
      return {
        ...state,
        currentState: actionTypes.OTP_SENT,
        user: {
          email: action.payload.email,
          otp_key: action.payload.otp_key
        }
      };
    case actionTypes.LOG_OUT:
      return {
        ...state,
        currentState: actionTypes.LOG_OUT,
        user: {}
      };
    default:
      return state;
  }
};

export default auth;
