import store from "../__store";
import { flashSuccess, flashErr, showLoader } from "../__store/actions";

export const alert = (msg, type) => {
    if (type == 'S') {
        store.dispatch(flashSuccess(msg));
    } else {
        store.dispatch(flashErr(msg));
    }
};


export const loader = (status) => {
    store.dispatch(showLoader(status));
};


export {
    alert as alertMsg
};