import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";


import { jsonToFormData } from "../../__helpers";
import service from "../../services/service";

import Review from "./review";
import Images from "./images";

export default function DoctorProfile(props) {

    const [viewState, setViewState] = useState('INIT');
    const [doctor, setDoctor] = useState({ overview: {}, reviews: [], locations: [], timings: [] });

    useEffect(() => {
        const searchParams = new URLSearchParams(props.location.search);
        setViewState('LOADING');
        let formData = {
            doctor_id: searchParams.get('id'), let: '', lng: ''
        }
        service.post("doctor-detail", jsonToFormData(formData), true).then(resp => {
            setViewState('LOADED');
            setDoctor(resp.result);
        }).catch(e => { setViewState('LOADED'); })
    }, [props.location.search]);

    return (<div className="mobile-responsive">
        <div className="container">
            <div>
                <nav>
                    <div className="float-left">
                        <Link to="/search" className="back-btn">
                            <img src="/assets/img/back-icon.svg" />
                        </Link>
                    </div>
                    <div className="float-right">
                        <ul className="list-group list-group-horizontal">
                            <li className="list-group-item list-icon-header">
                                <Link to="">
                                    <img src="/assets/img/bookmark_icon.svg" />
                                </Link>
                            </li>
                            <li className="list-group-item list-icon-header">
                                <Link to="">
                                    <img src="/assets/img/share_icon.svg" />
                                </Link>
                            </li>
                        </ul>
                    </div>
                    <div className="clearfix" />
                </nav>
                <div className="media doctor-profile mg-top-20">
                    <div className="align-self-start mr-3 doctor-img-layout">
                        <img src={doctor.profile_pic} className="doctor-img" />
                        <div className="layout-bottom text-center">
                            <img src="/assets/img/track_icon.svg" />
                            <span className="text-white">{doctor.distance}</span>
                        </div>
                    </div>
                    <div className="media-body">
                        <div className="specialization-mark mgbottom4">
                            <img src="/assets/img/medication_icon.svg" />
                            <span className="font14 weight500 color-doctor">{doctor.profile_title}</span>
                        </div>
                        <h6 className="mt-0 mgbottom4 weight600">{doctor.name}</h6>
                        <div className="mgbottom4" style={{ lineHeight: '18px' }}>
                            <span className="color-grey font14">
                                {doctor.speciality}
                            </span>
                        </div>
                        <div className="mgbottom4">
                            <span className="color-grey font14">{doctor.experience} of experience</span>
                        </div>
                        <Review stars={doctor.stars} no_of_users_rated={doctor.no_of_users_rated} />
                        <div>
                            <img className="cur-icon" src={doctor.currency_icon} />
                            <span className="font12 color-dark">{doctor.min_charges} - {doctor.max_charges} {doctor.currency_abbr}</span>
                            <span className="pd-left-5 color-grey">.</span>
                            <img className="pd-left-5" src="/assets/img/chatting_icon.svg" />
                            <img className="pd-left-5" src="/assets/img/call_icon.svg" />
                            <img className="pd-left-5" src="/assets/img/video_icon.svg" />
                            <img className="pd-left-5" src="/assets/img/visit_icon.svg" />
                        </div>
                    </div>
                </div>
                <div className="mg-top-20 filter-overflow">
                    <Images images={doctor.dept_photos} />
                </div>
                <div className="custom-tab mg-top-20">
                    <ul className="nav nav-pills nav-justified" id="pills-tab" role="tablist">
                        <li className="nav-item">
                            <a
                                className="nav-link active"
                                id="pills-home-tab"
                                data-toggle="pill"
                                href="#pills-overview"
                                role="tab"
                                aria-controls="pills-overview"
                                aria-selected="true"
                            >
                                Overview
                            </a>
                        </li>
                        <li className="nav-item">
                            <a
                                className="nav-link"
                                id="pills-profile-tab"
                                data-toggle="pill"
                                href="#pills-review"
                                role="tab"
                                aria-controls="pills-review"
                                aria-selected="false"
                            >
                                Review
                            </a>
                        </li>
                        <li className="nav-item">
                            <a
                                className="nav-link"
                                id="pills-contact-tab"
                                data-toggle="pill"
                                href="#pills-location"
                                role="tab"
                                aria-controls="pills-location"
                                aria-selected="false"
                            >
                                Location
                            </a>
                        </li>
                        <li className="nav-item">
                            <a
                                className="nav-link"
                                id="pills-contact-tab"
                                data-toggle="pill"
                                href="#pills-timing"
                                role="tab"
                                aria-controls="pills-timing"
                                aria-selected="false"
                            >
                                Timing
                            </a>
                        </li>
                    </ul>
                    <div className="tab-content" id="pills-tabContent">
                        <div
                            className="tab-pane fade show active mg-top-20"
                            id="pills-overview"
                            role="tabpanel"
                            aria-labelledby="pills-overview-tab"
                        >
                            <section id="aboutus">
                                <article>
                                    <p className="font14">
                                        {doctor.overview.description}
                                    </p>
                                </article>
                            </section>
                            <div className="border-bottom mg-top-20" />
                            <h5 className="mg-top-20 weight600">Services</h5>
                            <section id="services">
                                <article>
                                    <div className="list-group list-group-flush">
                                        {doctor.overview.services && doctor.overview.services.map((service, ind) => {
                                            return <div key={ind} className="list-group-item list-group-item-action pd-left-0">
                                                <span className="font14">{service.service}</span>
                                            </div>
                                        })}
                                    </div>
                                </article>
                            </section>
                            <div className="border-bottom mg-top-20" />
                            <h5 className="mg-top-20 weight600">Specializations</h5>
                            <section id="specializations">
                                <article>
                                    <div className="list-group list-group-flush">
                                        {doctor.overview.specializations && doctor.overview.specializations.map((specialization, ind) => {
                                            return <div key={ind} className="list-group-item list-group-item-action pd-left-0">
                                                <span className="font14">{specialization}</span>
                                            </div>
                                        })}
                                    </div>
                                </article>
                            </section>
                        </div>
                        <div
                            className="tab-pane fade"
                            id="pills-review"
                            role="tabpanel"
                            aria-labelledby="pills-review-tab"
                        >
                            <div className="list-group list-group-flush">
                                {doctor.reviews && doctor.reviews.map((review, ind) => {
                                    return <div key={ind} className="list-group-item list-group-item-action pdtb20">
                                        <div className="media doctor-review-list">
                                            <div className="mr-3 review-img-layout">
                                                <img src={review.user_pic} className="review-img" />
                                            </div>
                                            <div className="media-body">
                                                <h5 className="mt-0 font16 weight600 mgbottom4">
                                                    {review.user}
                                                </h5>
                                                <div className="mgbottom4">
                                                    <span className="color-grey font14 weight600">{review.review_date}</span>
                                                </div>
                                                <Review stars={review.star} />
                                                <p className="font14 margin-none">{review.review} </p>
                                            </div>
                                        </div>
                                    </div>
                                })}
                            </div>
                        </div>
                        <div
                            className="tab-pane fade"
                            id="pills-location"
                            role="tabpanel"
                            aria-labelledby="pills-location-tab"
                        >
                            {doctor.locations && doctor.locations.map((loc, ind) => {
                                return (<div key={ind} className="mg-top-20 location-map">
                                    <div id="googleMap" className="location-map-list">
                                        <iframe src={loc.map_url} />
                                    </div>
                                    <div className="address">
                                        <div className="list-group-item list-group-item-action pd8">
                                            <div className="media doctor-review-list">
                                                <div className="mr-3 review-img-layout margin-none">
                                                    <img
                                                        src="/assets/img/placeholder-img1.svg"
                                                        className="review-img"
                                                    />
                                                </div>
                                                <div className="media-body pd-left-5">
                                                    <h5 className="mt-0 font14 weight600 mgbottom4">
                                                        {loc.location}
                                                    </h5>
                                                    <div className="margin-none">
                                                        <span className="color-grey font12 weight600">
                                                            {loc.city}, {loc.country}
                                                        </span>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>)
                            })}
                        </div>
                        <div
                            className="tab-pane fade"
                            id="pills-timing"
                            role="tabpanel"
                            aria-labelledby="pills-timing-tab"
                        >
                            <div className="list-group list-group-flush mg-top-10">
                                {doctor.timings && doctor.timings.map((timing, ind) => {
                                    return (<div key={ind} className="list-group-item list-group-item-action pdtb10">
                                        <div className="media timing timing-active">
                                            <div className="mr-3 timing-days">{timing.day_1st_letter}</div>
                                            <div className="media-body">
                                                <div className="margin-none mt-0 ">
                                                    {timing.times.length > 0 && <span className="font14">{timing.times[0].from} - {timing.times[0].to}</span>}
                                                </div>
                                                <div className="margin-none">
                                                    {timing.times.length > 1 && <span className="font14">{timing.times[1].from} - {timing.times[1].to}</span>}
                                                </div>
                                            </div>
                                        </div>
                                    </div>)
                                })}
                            </div>
                        </div>
                    </div>
                </div>
                <div className="doctor-booking">
                    <a
                        data-toggle="modal"
                        data-target="#booking"
                        className="btn btn-doctor-color btn-block"
                    >
                        BOOK Appointment
                    </a>
                </div>
            </div>

        </div>
    </div>);
}