import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { shallowEqual, useSelector } from 'react-redux';

import { alertMsg, jsonToFormData } from "../../__helpers";
import service from "../../services/service";

export default function SaveMedicalRecord(props) {

    const [viewState, setViewState] = useState('ADD');
    const [form, setForm] = useState({ title: '' });

    const { auth } = useSelector(state => ({
        auth: { ...state.auth }
    }), shallowEqual);

    const handleChange = (e) => {
        setForm({
            ...form,
            [e.target.name]: e.target.value
        });
    }

    const saveMedicalRecord = (e) => {
        if (e) { e.preventDefault(); }

        if (viewState === 'ADD') { /* Add Attachment */
            let formData = new FormData(document.getElementById('save-medical-record-form'));

            service.post("upload-document", formData, true).then(resp => {
                alertMsg(resp.msg, 'S');
                props.history.push("/profile/medical-record");
            }).catch(e => { });
        }else {
            let formData = jsonToFormData(form);

            service.post("rename-document", formData, true).then(resp => {
                alertMsg(resp.msg, 'S');
                props.history.push("/profile/medical-record");
            }).catch(e => { });
        }

    }

    useEffect(() => {
        const searchParams = new URLSearchParams(props.location.search);

        if (searchParams.get("id") && searchParams.get("title")) {
            setViewState("RENAME");
            setForm({
                id: searchParams.get("id"),
                title: searchParams.get("title")
            });
        }
    }, [props.location.search]);


    return <div className="mobile-responsive">
        <div className="container">
            <nav>
                <div className="float-left">
                    <Link to="/profile/medical-record" className="back-btn">
                        <img src="/assets/img/back-icon.svg" />
                    </Link>
                </div>
                <div className="agnacare_logo heading height48 zIndexless">
                    <span className="text-center font18 margin-none">
                        {viewState === 'ADD' ? 'Add ' : 'Rename '} Medical Record
                    </span>
                </div>
                <div className="clearfix" />
            </nav>
            <form onSubmit={saveMedicalRecord} id="save-medical-record-form">
                <div data-toggle="modal" data-target="#fullname">
                    <label className="form-label color-doctor weight500 pd-top-30">
                        Title
                    </label>
                    <input type="text" name="title" value={form.title} onChange={handleChange} className="form-control input-style" placeholder="Title" />
                </div>
                {viewState === 'ADD' && <>
                    <h6 className="mg-top-20">Patient</h6>
                    <div className="filter-overflow height90">
                        <div className="patient-add text-center patient-select">
                            <div className="patient-img">
                                <img src={auth.user.profile_pic} />
                            </div>
                            <span className="font14">Me</span>
                        </div>
                        {/* <div className="patient-add text-center">
                        <div className="patient-img">
                            <img src="assets/img/doctor-img.jpg" />
                        </div>
                        <span className="font14">Brother</span>
                    </div>
                    <div className="patient-add text-center">
                        <div className="patient-img">
                            <img src="assets/img/doctor-img.jpg" />
                        </div>
                        <span className="font14">Sister</span>
                    </div> */}
                    </div>
                    <h6 className="mg-top-20">Attachment</h6>
                    <div className="attachment is-boxed">
                        <label className="attachment-label">
                            <input className="attachment-input" type="file" name="file" />
                            <span className="attachment-cta">
                                <div className="media">
                                    <div className="align-self-start mr-3">
                                        <span className="color-doctor font48">
                                            <i className="fas fa-cloud-upload-alt" />
                                        </span>
                                    </div>
                                    <div className="media-body color-doctor text-center">
                                        <div className="font16 height22 weight700">ADD ATTACHMENT</div>
                                        <div className="font14 height22">(Up to 5 MB)</div>
                                        <div className="font10 font-italic">
                                            JPG, PNG, PDF are supported.
                                    </div>
                                    </div>
                                </div>
                            </span>
                        </label>
                    </div>
                </>}
                <button type="submit" className="btn btn-doctor-color btn-block mg-top-20">
                    {viewState === 'ADD' ? 'UPLOAD Attachment ' : 'Rename Attachment'}
                </button>
            </form>
        </div>
    </div>;

}
