import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import moment from 'moment';

import service from "../../services/service";
import { alertMsg, jsonToFormData } from "../../__helpers";

import CountryCodeModal from "./country-code-modal";
import FullNameModal from "./full-name-modal";

export default function BasicInformation(props) {

    let toDay = moment().format("YYYY-MM-DD");
    const [form, setForm] = useState({});
    const [countryCodeModal, setCountryCodeModal] = useState(false);
    const [nameModal, setNameModal] = useState(false);

    /* Country Modal */
    const showCountryCodeModal = () => {
        setCountryCodeModal(true);
    }

    const hideMCountryCodeModal = () => {
        setCountryCodeModal(false);
    }

    const setIsdCode = (isd_code) => {
        setForm({ ...form, isd_code: isd_code });
    }

    /* Name Model */
    const showNameModal = () => {
        setNameModal(true);
    }

    const hideNameModal = () => {
        setNameModal(false);
    }

    const setName = (name) => {
        setForm({ ...form, ...name }); hideNameModal();
    }


    /* Page */
    const fullName = () => {
        return `${form.fname || ''} ${form.mname || ''} ${form.lname || ''}`
    }

    const handleChange = (e) => {
        setForm({
            ...form,
            [e.target.name]: e.target.value
        });
    }

    const handleChecked = (name, value) => {
        setForm({
            ...form,
            [name]: value
        });
    }

    /* Apis Call */

    const saveBasicInfo = (e) => {
        e.preventDefault();
        service.post("update-basic-info", jsonToFormData(form), true).then(resp => {
            alertMsg(resp.msg, "S");
            props.history.push("/profile");
        }).catch(e => { alertMsg(e.msg, "E"); })
    }

    useEffect(() => {
        service.post("user-detail", {}, true).then(resp => {
            let dob = resp.result.basic_info.dob;
            dob = (dob) ? moment(dob).format("YYYY-MM-DD") : '';
            setForm({
                ...resp.result.basic_info,
                dob: dob
            });
        }).catch(e => {});
    }, [])

    return (
        <div>
            <div className="mobile-responsive">
                <div className="container">
                    <form onSubmit={saveBasicInfo}>
                        <nav>
                            <div className="float-left">
                                <Link to="/profile" className="back-btn"><img src="/assets/img/back-icon.svg" /></Link>
                            </div>
                            <div className="agnacare_logo heading height48 zIndexless"><span className="text-center font18 margin-none">Basic Information</span></div>
                            <div className="float-right"><button type="submit" className="btn link-color-doctor text-decoration">Save</button></div>
                            <div className="clearfix" />
                        </nav>
                        <div onClick={showNameModal}>
                            <label className="form-label color-doctor weight500 pd-top-30">Full Name</label>
                            <input type="text" className="form-control input-style" value={fullName()} disabled />
                        </div>
                        <label className="form-label color-doctor weight500 pd-top-20">Email</label>
                        <input type="email" className="form-control input-style" name="email" value={form.email || ''} onChange={handleChange} placeholder="Email" required />
                        <label className="form-label color-doctor weight500 pd-top-20">DOB</label>
                        <input type="date" className="form-control input-style" max={toDay} name="dob" value={form.dob || ''} onChange={handleChange} placeholder="DD/MM/YYYY" required />
                        <label className="form-label color-doctor weight500 pd-top-20">Phone Number</label>
                        <div className="form-row">
                            <div className="form-group col-3">
                                <input type="text" readOnly={true} className="form-control input-style" placeholder="Code" value={form.isd_code} onClick={showCountryCodeModal} required />
                            </div>
                            <div className="form-group col-9">
                                <input type="tel" className="form-control input-style" name="mob" value={form.mob || ''} onChange={handleChange} placeholder="Phone Number" required />
                            </div>
                        </div>
                        <label className="form-label color-doctor weight500 pd-top-20">Gender</label>
                        <div className="mg-top-10">
                            <div className="form-check form-check-inline">
                                <label className="radio-button">Male
                                    <input type="radio" name="gender" value="Male" checked={form.gender === 'Male'} onChange={(e) => { handleChecked('gender', 'Male'); }} required />
                                    <span className="checkmark" />
                                </label>
                            </div>
                            <div className="form-check form-check-inline">
                                <label className="radio-button">Female
                                    <input type="radio" value="Female" checked={form.gender === 'Female'} onChange={(e) => { handleChecked('gender', 'Female'); }} required />
                                    <span className="checkmark" />
                                </label>
                            </div>
                            <div className="form-check form-check-inline">
                                <label className="radio-button">Other
                                    <input type="radio" name="gender" value="Other" checked={form.gender === 'Other'} onChange={(e) => { handleChecked('gender', 'Other'); }} required />
                                    <span className="checkmark" />
                                </label>
                            </div>
                            <div className="border-bottom" />
                        </div>
                        <label className="form-label color-doctor weight500 pd-top-30">Location</label>
                        <input type="text" className="form-control input-style" name="location" value={form.location || ''} onChange={handleChange} placeholder="Location" />
                        <label className="form-label color-doctor weight500 pd-top-30">{form.idcard_name}</label>
                        <input type="text" className="form-control input-style" minLength={form.idno_length} maxLength={form.idno_length} name="idno" value={form.idno || ''} onChange={handleChange} placeholder={`Enter your ${form.idcard_name}`} required />
                        {/* <span className="color-grey mg-top-10 weight300">(Aadhar Card No., Civil ID No., License No., etc)</span> */}
                        <div className="clearfix" />
                        <label className="form-label color-doctor weight500 pd-top-30">Blood GROUP</label>
                        <select className="custom-select form-control input-style" name="blood_group" value={form.blood_group || ''} onChange={handleChange} required>
                            <option value="">Choose your blood group</option>
                            <option value="A">A</option>
                            <option value="A+">A+</option>
                            <option value="B+">B+</option>
                        </select>
                        <label className="form-label color-doctor weight500 pd-top-30">Height</label>
                        <input type="text" className="form-control input-style" name="height" value={form.height || ''} onChange={handleChange} placeholder="Enter your height" required />
                        <label className="form-label color-doctor weight500 pd-top-30">Weight</label>
                        <input type="text" className="form-control input-style" name="weight" value={form.weight || ''} onChange={handleChange} placeholder="Enter your weight" required />
                    </form>
                </div>
            </div>

            <CountryCodeModal show={countryCodeModal} onHide={hideMCountryCodeModal} setData={setIsdCode} value={form.isd_code} />
            <FullNameModal show={nameModal} onHide={hideNameModal} setName={setName} name={{ fname: form.fname, mname: form.mname, lname: form.lname }} />
        </div>
    );
}
