import React from "react";
import { withRouter, Route, Switch, useRouteMatch } from "react-router-dom";

import { AuthRoute } from "./authRoute";

/* Default Layout */
import DefaultLayout from "../components/layout/default-layout";
import Homepage from "../components/homepage/homepage";
import Search from "../components/search/search";

/* Blank Layout */
import BlankLayout from "../components/layout/blank-layout";
import Login from "../components/login/login";
import Register from "../components/register/register";
import Otp from "../components/otp/otp";
import Profile from "../components/profile/index";
import BasicInformation from "../components/profile/basic-information";
import Medical from "../components/profile/medical";
import Lifestyle from "../components/profile/lifestyle";
import MyDoctor from "../components/profile/my-doctor";
import MedicalRecord from "../components/profile/medical-record";
import SaveMedicalRecord from "../components/profile/save-medical-record";
import DoctorProfile from "../components/doctor/profile";


function PageRoutes(props) {

	const match = useRouteMatch();

	let routes = {
		default: [
			"/", "/search",
		],
		blank: [
			"/login", "/register", "/otp", "/profile", "/profile/basic-information", "/profile/medical",
			"/profile/lifestyle", "/profile/my-doctor", "/doctor/profile", "/profile/medical-record",
			"/profile/save-medical-record"
		]
	};

	return (
		<React.Fragment>

			{routes.default.indexOf(props.location.pathname) > -1 && <>
				<DefaultLayout {...props}>
					<Switch>
						<Route path={match.path} exact component={Homepage} />
						<Route path={`${match.path}search`} exact component={Search} />
					</Switch>
				</DefaultLayout>
			</>}
			{routes.blank.indexOf(props.location.pathname) > -1 && <>
				<BlankLayout {...props}>
					<Switch>
						<Route path={`${match.path}login`} exact component={Login} />
						<Route path={`${match.path}register`} exact component={Register} />
						<Route path={`${match.path}otp`} exact component={Otp} />
						<AuthRoute path={`${match.path}profile`} exact component={Profile} />
						<AuthRoute path={`${match.path}profile/basic-information`} exact component={BasicInformation} />
						<AuthRoute path={`${match.path}profile/medical`} exact component={Medical} />
						<AuthRoute path={`${match.path}profile/lifestyle`} exact component={Lifestyle} />
						<AuthRoute path={`${match.path}profile/my-doctor`} exact component={MyDoctor} />
						<AuthRoute path={`${match.path}profile/medical-record`} exact component={MedicalRecord} />
						<AuthRoute path={`${match.path}profile/save-medical-record`} exact component={SaveMedicalRecord} />
						<AuthRoute path={`${match.path}doctor/profile`} exact component={DoctorProfile} />
					</Switch>
				</BlankLayout>
			</>}
		</React.Fragment>
	);
}

export default withRouter(PageRoutes);
