import React, { useState, useEffect } from "react";
import { useDispatch } from 'react-redux';
import { sentOtp } from "../../__store/actions";

import { alertMsg, jsonToFormData } from "../../__helpers";
import service from "../../services/service";

import CountryCodeModal from "../profile/country-code-modal";

export default function Register(props) {

	const [form, setForm] = useState({
		fname: '', mname: '', lname: '', email: '', isd_code: '+91', mob: '', gender: '', terms_and_cond: false
	});

	const dispatch = useDispatch();

	const [countryCodeModal, setCountryCodeModal] = useState(false);


	const showCountryCodeModal = () => {
		setCountryCodeModal(true);
	}

	const hideMCountryCodeModal = () => {
		setCountryCodeModal(false);
	}

	const handleChange = (e) => {
		setForm({
			...form,
			[e.target.name]: e.target.value
		});
	}

	const handleChecked = (name, value) => {
		setForm({
			...form,
			[name]: value
		});
	}

	const handleSubmit = (e) => {
		e.preventDefault();

		service.postRequestWithoutToken("signup", jsonToFormData(form), true).then(resp => {
			alertMsg(resp.msg, 'S');
			dispatch(sentOtp({
				email: form.email,
				otp_key: resp.otp_key
			}));
			props.history.push("/otp");
		}).catch(e => {})
	}

	const setIsdCode= (isd_code) => {
		setForm({
			...form,
			isd_code: isd_code
		});
	}

	return (
		<div>
			<div className="mobile-responsive">
				<div className="container pd-top-40">
					<img className="login-logo" src="assets/img/fav-logo.svg" />
					<h3 className="pd-top-20 weight700">Sign Up to Agnacare</h3>

					<form onSubmit={handleSubmit}>
						<label className="form-label color-doctor weight500 pd-top-30">First Name</label>
						<input name="fname" value={form.fname} onChange={handleChange} type="text" className="form-control input-style" placeholder="First Name" required />

						<label className="form-label color-doctor weight500 pd-top-20">Last Name</label>
						<input name="lname" value={form.lname} onChange={handleChange} type="text" className="form-control input-style" placeholder="Last Name" required={true} />

						<label className="form-label color-doctor weight500 pd-top-20">Email</label>
						<input name="email" value={form.email} onChange={handleChange} type="email" className="form-control input-style" placeholder="Email" required={true} />

						<label className="form-label color-doctor weight500 pd-top-20">Phone Number</label>
						<div className="form-row">
							<div className="form-group col-3">
								<input type="text" readOnly={true} className="form-control input-style" placeholder="Code" value={form.isd_code} onClick={showCountryCodeModal} />
							</div>
							<div className="form-group col-9">
								<input name="mob" value={form.mob} onChange={handleChange} type="tel" className="form-control input-style" placeholder="Phone Number" required={true} />
							</div>
						</div>

						<label className="form-label color-doctor weight500 pd-top-20">Gender</label>
						<div className="mg-top-10">
							<div className="form-check form-check-inline">
								<label className="radio-button">Male
                        			<input type="radio" className="form-control" name="gender" value="Male" checked={form.gender === 'Male'} onChange={(e) => { handleChecked('gender', 'Male'); }} required={true} />
									<span className="checkmark"></span>
								</label>
							</div>
							<div className="form-check form-check-inline">
								<label className="radio-button">Female
                        			<input type="radio" name="gender" value="Female" checked={form.gender === 'Female'} onChange={(e) => { handleChecked('gender', 'Female'); }} required={true} />
									<span className="checkmark"></span>
								</label>
							</div>
							<div className="form-check form-check-inline">
								<label className="radio-button">Other
                        			<input type="radio" name="gender" value="Other" checked={form.gender === 'Other'} onChange={(e) => { handleChecked('gender', 'Other'); }} required={true} />
									<span className="checkmark"></span>
								</label>
							</div>
							<div className="border-bottom"></div>
						</div>

						<div className="form-check form-check-inline mg-top-20">
							<label className="check-box">
								<span className="color-grey">By signing up, you agree to Agnacare
									<a href="terms.html" className="link-color-doctor">Term of Service</a> and
									<a href="privacy.html" className="link-color-doctor">Privacy Policy</a>.
								</span>
								<input type="checkbox" name="terms_and_cond" value="terms" checked={form.terms_and_cond} onChange={(e) => { handleChecked('terms_and_cond', !form.terms_and_cond) }} required={true} />
								<span className="check-mark"></span>
							</label>
						</div>

						<button type="submit" className="btn btn-doctor-color ripple btn-block mg-top-20">Sign Up</button>
					</form>

					<div className="mg-top-30">
						<span className="color-grey">Already have an account?</span>
						<a href="index.html" className="link-dark">Log In</a>
					</div>
				</div>
			</div>

			<CountryCodeModal show={countryCodeModal} onHide={hideMCountryCodeModal} setData={setIsdCode} value={form.isd_code} />
		</div>
	);
}
