import React, { useState, useEffect } from "react";
import Modal from 'react-bootstrap/Modal';
import service from "../../services/service";

export default function CountryCodeModal(props) {

    const [countryCodes, setCountryCode] = useState([]);
    const [filteredList, setFilteredList] = useState([]);


    const handleChange = (e) =>{
        let val = e.target.value;
        
        let filteredList = countryCodes.filter((row)=>{
            
            if(row.title.includes(val)){
                return true;
            }

            if(row.code.includes(val)){
                return true;
            }
            return false;
        })

        setFilteredList(filteredList);
    }


    useEffect(() => {
        service.postRequestWithoutToken("countries", {}).then(resp => {
            let countryCode = [];

            resp.result.map((country) => {
                countryCode.push({
                    code: country.isd_code, title: country.country_name,
                })
            })

            setCountryCode(countryCode);
            setFilteredList(countryCode);
        }).catch(e => { })
    }, []);

    return (<>
        <Modal
            show={props.show}
            onHide={props.onHide}
            backdrop="static"
            keyboard={false}
            className="animate__animated animate__fadeInUp custom-model"
        >
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="exampleModalLabel">Select Country Code</h5>
                        <span className="back-btn" onClick={props.onHide}><img src="/assets/img/close-icon.svg" /></span>
                    </div>
                    <div className="modal-body">
                        <form>
                            <div className="search-box">
                                <input type="search" onChange={handleChange} className="form-control search" placeholder="Search country" />
                            </div>
                        </form>
                        <div className="list-group list-group-flush mg-top-30">
                            {filteredList.map((row, ind) => {
                                return (<span key={ind} className={`list-group-item list-group-item-action ${props.value == row.code ? 'list-active' : ''}`}>
                                    <div onClick={() => { props.setData(row.code); }}>
                                        <span className="width75">({row.code})</span>
                                        <span className="">{row.title}</span>
                                    </div>
                                </span>);
                            })}
                        </div>
                    </div>
                </div>
            </div>
        </Modal>
    </>);
}