import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import { jsonToFormData } from "../../__helpers";
import service from "../../services/service";

import DepartmentListModal from "./department-list-modal";

export default function Homepage() {

    const [departments, setDetaprtment] = useState([]);
    const [departments5, setDetaprtment5] = useState([]);
    const [doctors, setDoctor] = useState([]);
    const [doctors5, setDoctor5] = useState([]);

    const [departmentModal, setDepartmentModal] = useState(false);

    const getHomeScreenDeatil = (location) => {

        let formData = {
            lat: location.coords.latitude, lng: location.coords.longitude
        }

        service.post('/home-screen-details', jsonToFormData(formData), true).then(resp => {
            if (resp.departments.length > 0) {
                setDetaprtment(resp.departments);
                setDetaprtment5(resp.departments.slice(0, 5));
            }

            if (resp.doctors.length > 0) {
                setDoctor(resp.doctors);
                setDoctor5(resp.doctors);
            }
        }).catch(err => { })
    }

    useEffect(() => {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(getHomeScreenDeatil);
        }
    }, [])

    return (
        <div>
            <div className="mobile-responsive mg-bottom-70">
                <div className="container pd-top-20">
                    <nav>
                        <div className="float-left"><a data-toggle="modal" data-target="#location"><img src="assets/img/location-icon.svg" /> Sharq</a></div>
                        <div className="agnacare_logo heading zIndexless"><img src="assets/img/agnacare_logo.png" /></div>
                        <div className="float-right"><a data-toggle="modal" data-target="#notifications"><img src="assets/img/notification-icon.svg" /></a></div>
                        <div className="clearfix" />
                    </nav>
                    <div className="mg-top-20">
                        <Link to="/search" className="search-box">
                            <input type="search" className="form-control search search-disabled" placeholder="Find Doctors, Specialities, Clinics, etc" disabled />
                        </Link>
                    </div>
                    <div className="row mg-top-10">
                        {departments5.map((row, ind) => {
                            return <div key={ind} className="col-4 text-center mgtb10">
                                <Link to={`/search?department_id=${row.dept_id}`} className="specialities-home">
                                    <div className="icon">
                                        <img src={row.icon} />
                                    </div>
                                    <span className="color-dark weight600 mg-top-10">{row.dept_name}</span>
                                </Link>
                            </div>;
                        })}

                        {departments.length > 5 && <div className="col-4 text-center mgtb10">
                            <button onClick={() => { setDepartmentModal(true) }} type="button" className="btn specialities-home">
                                <div className="icon"><img src="/assets/img/more-s.svg" /></div>
                                <span className="color-dark weight600 mg-top-10">more</span>
                            </button>
                        </div>
                        }
                    </div>
                    <div className="banner mg-top-30">
                    </div>
                </div>
            </div>

            <DepartmentListModal show={departmentModal} onHide={() => { setDepartmentModal(false) }} lists={departments} />

        </div>
    );
}
