import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import service from "../../services/service";

import DoctorList from "../search/doctor-list";

export default function MyDoctor(props) {

    const [viewState, setViewState] = useState('INIT');
    const [doctors, setDoctors] = useState([]);

    useEffect(() => {
        setViewState('LOADING');
        service.post("mydoctors", {}, true).then(resp => {
            setViewState('LOADED');
            setDoctors(resp.result);
        }).catch(e => { setViewState('LOADED'); })
    }, []);

    return (<div className="mobile-responsive">
        <div className="container">
            <nav>
                <div className="float-left">
                    <Link to="/profile" className="back-btn"><img src="/assets/img/back-icon.svg" /></Link>
                </div>
                <div className="agnacare_logo heading height48 zIndexless"><span className="text-center font18 margin-none">My Doctor List</span></div>
                <div className="clearfix" />
            </nav>

            <div>
                <DoctorList doctors={doctors} />
                {viewState === 'LOADING' && <span>Loading ...</span>}
                {(doctors.length === 0 && viewState === 'LOADED') && <div className="alert alert-danger">
                    No Doctor Found In List
                    </div>}
            </div>
        </div>
    </div>);
}