import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Alert from 'react-bootstrap/Alert';

import { alertMsg, jsonToFormData } from "../../__helpers";
import service from "../../services/service";

export default function MedicalRecord(props) {

    const [viewState, setViewState] = useState('INIT');
    const [documents, setDocument] = useState([]);

    const deleteDocument = (id) => {

        if (!window.confirm("Are You Sure To Delete This Record?")) {
            return;
        }

        setViewState('LOADING');
        service.post("delete-document", jsonToFormData({ id: id }), true).then(resp => {
            alertMsg(resp.msg, "S");
            documentList();
        }).catch(e => { setViewState('LOADED'); });
    }

    const documentList = () => {
        setViewState('LOADING');
        service.post("documents", {}, true).then(resp => {
            setViewState('LOADED');
            setDocument(resp.result);
        }).catch(e => { setViewState('LOADED'); });
    }

    useEffect(() => { documentList(); }, []);

    return <div>
        <div className="mobile-responsive mg-bottom-70">
            <div className="container pd-top-20">
                <h3 className="weight700 text-center">Medical Records</h3>
                <div className="mg-top-20">
                    <Link to="/profile/save-medical-record" className="btn-block btn btn-attachment">
                        <i className="fas fa-cloud-upload-alt" /> ADD ATTACHMENT
                    </Link>
                </div>

                {documents && documents.map((document, ind) => {

                    return <div key={ind} className="message-list mg-top-20">
                        <a href></a>
                        <div className="media message-profile">
                            <a href>
                                <div className="align-self-start mr-3">
                                    <div className="message-img">
                                        <img
                                            style={{ objectFit: "none" }}
                                            src={`/assets/img/${document.is_pdf == 1 ? 'pdf_icon.svg' : 'jpg_icon.svg'}`}
                                        />
                                    </div>
                                </div>
                            </a>
                            <div className="media-body">
                                <a href></a>
                                <div>
                                    <a href>
                                        <div className="float-left">
                                            <h6 className="color-dark mt-0 mgbottom4 weight600">
                                                {document.title}
                                            </h6>
                                            <div>
                                                <span className="color-grey font14">{document.patient_name}</span>
                                                <span className="color-grey font14">.</span>
                                                <span className="color-grey font14">{document.file_size}</span>
                                                <span className="color-grey font14">.</span>
                                                <span className="color-grey font14">{document.uploaded_on}</span>
                                            </div>
                                        </div>
                                    </a>
                                    <div className="float-right">
                                        <div className="btn-group">
                                            <a
                                                className="color-grey font18 pd12"
                                                data-toggle="dropdown"
                                                aria-haspopup="true"
                                                aria-expanded="false"
                                            >
                                                <i className="fas fa-ellipsis-h" />
                                            </a>
                                            <div className="dropdown-menu dropdown-menu-right box-shadow border">
                                                <Link to={`/profile/save-medical-record?id=${document.id}&title=${document.title}`} className="dropdown-item">
                                                    Rename
                                                </Link>
                                                <button onClick={() => { deleteDocument(document.id) }} className="dropdown-item" type="button">
                                                    Delete
                                                </button>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="clearfix" />
                                </div>
                            </div>
                        </div>
                    </div>;
                })}

                {(viewState === 'LOADED' && documents.length === 0) && <Alert variant='danger' className="mt-3">No Medical Record Found</Alert>}

            </div>
        </div>
    </div>
}
