import React, { useState, useEffect } from "react";
import Modal from 'react-bootstrap/Modal';
import { Link } from "react-router-dom";


export default function DepartmentListModal(props) {

    const [lists, setList] = useState([]);
    const [filteredList, setFilteredList] = useState([]);


    const handleChange = (e) => {
        let val = e.target.value;

        let filteredList = lists.filter((row) => {
            return row.dept_name.toLowerCase().includes(val.toLowerCase());
        })

        setFilteredList(filteredList);
    }


    useEffect(() => {
        setList(props.lists);
        setFilteredList(props.lists);
    }, [props.lists]);

    return (<>
        <Modal
            show={props.show}
            onHide={props.onHide}
            backdrop="static"
            keyboard={false}
            className="animate__animated animate__fadeInUp custom-model"
        >
            <div className="modal-dialog">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title">Select Specialities</h5>
                        <span className="back-btn" onClick={props.onHide}><img src="/assets/img/close-icon.svg" /></span>
                    </div>
                    <div className="modal-body">
                        <form>
                            <span className="search-box">
                                <input type="search" onChange={handleChange} className="form-control search" placeholder="Search for Specialities" />
                            </span>
                        </form>
                        <div className="list-group list-group-flush mg-top-20">
                            {filteredList.map((row, ind) => {
                                return <Link key={ind} to={`/search?department_id=${row.dept_id}`} className="list-group-item pd0 list-group-item-action specialities-list">
                                    <div className="listing">
                                        <div className="icon"><img src={row.icon} /></div>
                                        <span className="color-dark weight600 lineheight36">{row.dept_name}</span>
                                    </div>
                                </Link>
                            })}
                        </div>
                    </div>
                </div>
            </div>
        </Modal>
    </>);
}