import React from "react";


export default function Review(props) {

    return (<div className={`rating-star mg-top-15 ${(props.stars > 3 ? '' : 'norating')}`}>
        <ul className="float-left">
            {[1, 2, 3, 4, 5].map((val, ind) => {
                let img_src;
                if ((val - props.stars) == .5) {
                    img_src = '/assets/img/half_star_icon.svg'
                } else if (val > props.stars) {
                    img_src = '/assets/img/blank_icon.svg'
                } else {
                    img_src = '/assets/img/star_icon.svg';
                }
                return <li key={ind} className='star'><img src={img_src} /></li>;
            })}
        </ul>
        {props.no_of_users_rated != undefined &&
            <span className={`font14 pd-left-5 ${props.stars > 3 ? 'color-orange' : 'color-grey'}`}>{props.no_of_users_rated || 0}</span>
        }
        <div className="clearfix" />
    </div>);
}