import * as actionTypes from "../../__constant/reducer";


const initialState = {
    isLoading: false,
};
var initialMiniLoaderState = [];

const loader = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.IS_LOADING:
            return {
                ...state,
                isLoading: action.payload
            };
        default:
            return state;
    }
};

export default loader;
